.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  font-weight: 300;
  padding: 20px 0 20px 20px;
  color: gray;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  word-break: normal;
  min-width: 500px;
}

.movie img {
  position: relative;
  max-width: 120px;
  margin-right: 30px;
  top: -40px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year {
  font-weight: 500;
  margin: 0;
}

.movie .movie__title {
  margin-bottom: 10px;
  font-size: 25px;
  color: black;
}
.movie .movie__year {
  opacity: 0.4;
  font-size: 15px;
}

.movie .movie__genres {
  list-style: none;
  padding: 10px;
  margin: 5px 0;
  display: flex;
}

.movie .movie__genres li {
  margin-right: 5px;
}
